/* AboutSection.module.css */
.title{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.about .content{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    position: relative;
}

.about .content .colLeft{
    width: 45%;
    position: relative;
}

.about .content .colRight{
    width: 50%;
    position: relative;
}

.about .content .colRight .contentTitle{
    color: var(--brown-3-color);
    font-size: 1.8em;
    font-weight: 600;
    letter-spacing: 0.5px;
    position: relative;

}

.about .content .colRight .paragraph{
    color: var(--brown-4-color);
    font-size: 1.2em;
    font-weight: 400;
    letter-spacing: 0.5px;
    line-height: 30px;
    position: relative;
}
.imageDiv{
    margin-left: 30px;
}
.image {
    border-radius: 50% 50% 0 0;
    border: 1px solid var(--pink-4-color);
    height: 300px;
    width: 300px;
    object-fit: cover;
    display: block;
}

@media screen and (max-width: 768px) {
    .content {
        flex-direction: column;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .about .content .colRight{
        margin: 40px 0 0 30px;
        width: 300px;
    }
    .colLeft {
        margin: 0 0 0 -100px;
    }
    .imageDiv{
        margin-left: 0px;
    }
    
}
